import { ActionSheet, Text, View } from 'react-native-ui-lib';
import { colors } from '../../../assets/color/color';
import {
  Dimensions,
  Image,
  SafeAreaView,
  StyleSheet,
  TouchableOpacity,
} from 'react-native';
import { image_close, image_rightArrow12px } from '../../../assets/images/images';
import { useState } from 'react';

const InstallmentScheduleItem = (installmentObj: any) => {
  const [visible, setVisible] = useState(false);

  const renderTitleCustome = () => {
    return (
      <View
        key={installmentObj.itemInstallmentSchedule.id}
        style={{
          flexDirection: 'row',
          width: '100%',
          height: '20%',
          justifyContent: 'space-around',
          paddingLeft: '28%',
          alignItems: 'center',
          borderBottomWidth: 0.5,
          borderBottomColor: "rgba(0, 53, 128, 0.15)",
          borderRadius: 12,
          borderColor: 'grey',
        }}>
        <Text
          style={{
            fontWeight: '600',
            lineHeight: 22,
            letterSpacing: 0,
            textAlign: 'center',
            fontSize: 14,
            color: colors.colorText,
          }}>
          {installmentObj.itemInstallmentSchedule.date}
        </Text>
        <TouchableOpacity onPress={() => setVisible(false)}>
          <Image style={{ height: 10, width: 10 }} source={image_close} />
        </TouchableOpacity>
      </View>
    );
  };

  const renderActionView = () => {
    return (
      <View
        key={installmentObj.itemInstallmentSchedule.id}
        style={{
          marginHorizontal: 24,
          alignItems: 'center',
          justifyContent: 'center',
        }}>
        <View style={styles.rowAcs}>
          <Text style={styles.labelActionSheet}>Tiền gốc </Text>
          <Text style={styles.labelActionSheet}>
            {installmentObj.itemInstallmentSchedule.orgMoney}
          </Text>
        </View>
        <View style={styles.rowAcs}>
          <Text style={styles.labelActionSheet}>Tiền lãi </Text>
          <Text style={styles.labelActionSheet}>
            {installmentObj.itemInstallmentSchedule.interest}
          </Text>
        </View>
        <View style={styles.rowAcs}>
          <Text style={styles.labelActionSheet}>Tiền gốc và lãi </Text>
          <Text style={styles.valueActionSheet}>
            {installmentObj.itemInstallmentSchedule.totalOrgItr}
          </Text>
        </View>
        <View style={styles.rowAcs}>
          <Text style={styles.labelActionSheet}>Số tiền còn lại </Text>
          <Text style={styles.valueActionSheet}>
            {installmentObj.itemInstallmentSchedule.remaining}
          </Text>
        </View>
      </View>
    );
  };

  const showModal = () => {
    setVisible(true);
    renderActionView();
  };

  const closeModal = () => {
    setVisible(false);
  };

  return (
    <SafeAreaView>
      <View
        style={{
          alignItems: 'center',
          flexDirection: 'row',
          justifyContent: 'space-between',
          paddingVertical: 24,
        }}>
        <View>
          <Text
            style={{
              fontSize: 14,
              fontWeight: '400',
              color: colors.colorText,
            }}>
            {installmentObj.itemInstallmentSchedule.date}
          </Text>
        </View>
        <View
          style={{
            flexDirection: 'row',
            alignItems: 'center',
            justifyContent: 'center',
          }}>
          <Text>
            {installmentObj.itemInstallmentSchedule.totalOrgItr}
          </Text>
          <TouchableOpacity style={{}} onPress={() => showModal()}>
            <Image
              style={{ marginLeft: 12, width: 6, height: 12 }}
              source={image_rightArrow12px}
            />
          </TouchableOpacity>
        </View>
        <ActionSheet
          key={installmentObj.itemInstallmentSchedule.id}
          cancelButtonIndex={1}
          renderTitle={renderTitleCustome}
          visible={visible}
          containerStyle={{
            height: Dimensions.get('screen').height * 0.3,
          }}
          dialogStyle={{ borderTopRightRadius: 12, borderTopLeftRadius: 12 }}
          onDismiss={() => closeModal()}
          renderAction={() => renderActionView()}
          destructiveButtonIndex={0}
          options={[
            {
              label: `${installmentObj.itemInstallmentSchedule.id}`,
              onPress: () => closeModal(),
            },
          ]}
        />
      </View>
    </SafeAreaView>
  );
};

const styles = StyleSheet.create({
  labelActionSheet: {
    fontSize: 14,
    lineHeight: 22,
    fontWeight: '400',
    color: colors.colorText,
  },
  valueActionSheet: {
    fontSize: 14,
    lineHeight: 22,
    fontWeight: '500',
    color: colors.colorText,
  },
  rowAcs: {
    flexDirection: 'row',
    width: '100%',
    height: '20%',
    justifyContent: 'space-between',
  },
});

export default InstallmentScheduleItem;
