import React, { useCallback, useEffect, useState } from "react";
import { FlatList, SafeAreaView, ScrollView } from "react-native";
import Header from "../../component/AppBar";
import { Chip, GridList, View } from "react-native-ui-lib";
import InputComponent from "../../component/InputComponent";
import { StatusContract } from "../../utlis/enum";
import ManageItemComponent from "./ManageItemComponent";
import ScrollHoriz from "../../component/ScrollHoriz";
import { dataManageContract } from "../../utlis/data";

const statusContractList = [
  { id: 1, name: "Tất cả ", status: StatusContract.all, selected: true },
  { id: 2, name: "Hoàn thành", status: StatusContract.success, selected: false },
  { id: 3, name: "Đang xử lý", status: StatusContract.processing, selected: false },
  { id: 4, name: "Hủy bỏ", status: StatusContract.cancel, selected: false },
  { id: 5, name: "Từ chối", status: StatusContract.reject, selected: false },
  { id: 6, name: "Quá hạn", status: StatusContract.expried, selected: false },
]

const MainManagerContract = (props: any) => {
  const [listStatus, setListStatus] = useState(statusContractList)
  const [chipSelected, setChipSelected] = useState(statusContractList[0])
  const [listData, setListData] = useState(dataManageContract)
  const [search, changeSearch] = useState('')

  useEffect(() => {
    console.log(chipSelected);

    if (chipSelected.status == StatusContract.all) {
      setListData(dataManageContract);
    } else {
      const newListData = dataManageContract.filter((data) => data.status === chipSelected.status)
      setListData(newListData)
    }

  }, [JSON.stringify(chipSelected)])

  return (
    <SafeAreaView style={{ flex: 1, backgroundColor: "#fff" }}>
      <Header label='Quản lý hợp đồng' {...props} />
      <View style={{ paddingHorizontal: 24, paddingVertical: 17 }}>
        <InputComponent text={search} onChangeText={changeSearch} placeholder="Nhập để tìm kiếm" />
      </View>
      <ScrollHoriz listStatus={statusContractList} setChipSelected={setChipSelected} />
      {listData && (
        <GridList
          numColumns={1}
          listPadding={24}
          data={listData}
          showsHorizontalScrollIndicator={false}
          showsVerticalScrollIndicator={false}
          renderItem={(item) => <ManageItemComponent {...props} ItemManage={item} />}
        />
      )}

    </SafeAreaView>
  )
}

export default MainManagerContract