import React, { useState, Fragment, useCallback, useMemo, useRef, useEffect } from 'react';
import { StyleSheet, View, ScrollView, Text, TouchableOpacity } from 'react-native';
import { Calendar, CalendarUtils } from 'react-native-calendars';
import { Button, Dialog, Image, Modal } from 'react-native-ui-lib';
import { image_left_arrowBo } from '../../assets/images/images';
import { colors } from '../../assets/color/color';

const CalendarScreen = (props: any) => {
  const [selected, setSelected] = useState((new Date().toISOString()).split("T")[0]);
  const [currentMonth, setCurrentMonth] = useState('');
  const [currentYear, setCurrentYear] = useState('');

  const getDate = (count: number) => {
    const date = new Date(selected);
    const newDate = date.setDate(date.getDate() + count);
    return CalendarUtils.getCalendarDateString(newDate);
  };
  // useEffect(() => {
  //   const toDateString = new Date().toDateString();
  //   console.log(toDateString);
  //   const listDate = toDateString.split(" ")
  //   setCurrentMonth(listDate[1])
  //   setCurrentYear(listDate[3])
  // }, [])

  const renderCalendarWithPeriodMarkingAndDotMarking = () => {
    return (
      <Fragment>
        <Calendar
          headerStyle={{
            borderWidth: 1,
            borderStyle: "solid",
            borderTopColor: "rgba(0, 53, 128, 0.15)",
            borderLeftColor: "transparent",
            borderRightColor: "transparent",
            borderBottomColor: "rgba(0, 53, 128, 0.15)"
          }}
          enableSwipeMonths={true}
          current={selected}
          initialDate={selected}
          minDate={getDate(-14)}
          showWeekNumbers
          allowSelectionOutOfRange
          markingType={'custom'}
          markedDates={{
            [selected]: {
              selected: true,
              disableTouchEvent: true,
              selectedColor: colors.primary,
              selectedTextColor: '#fff'
            },
            // [selected]: { customContainerStyle: { borderColor: colors.primary, borderWidth: 1, borderStyle: "solid" }, textColor: "red" },
            [getDate(1)]: { selected: true, disableTouchEvent: true },
            [getDate(2)]: { selected: true, disableTouchEvent: true },
            [getDate(3)]: { selected: true, disableTouchEvent: true },
            [getDate(4)]: { selected: true, disableTouchEvent: true },
            [getDate(5)]: { selected: true, activeOpacity: 1, disableTouchEvent: true },
            [getDate(6)]: { selected: true, activeOpacity: 1, disableTouchEvent: true },
          }}
          theme={{
            textInactiveColor: colors.primary,
            textSectionTitleDisabledColor: 'grey',
            textSectionTitleColor: colors.primary,
            arrowColor: colors.primary,
            todayTextColor: "#fff",
            todayBackgroundColor: colors.primary,
            selectedDayTextColor: "#fff",
            selectedDayBackgroundColor: colors.primary,
          }}
          onDayPress={(day) => {
            console.warn(`${day.day} pressed`)
            setSelected(day.dateString)
          }}
        />

      </Fragment>
    );
  };


  const setCustomHeaderNewMonth = (next = false) => {
    const add = next ? 1 : -1;
    const month = new Date(selected);
    const newMonth = new Date(month.setMonth(month.getMonth() + add));
    setCurrentMonth(newMonth.toISOString().split('T')[0]);
  };

  const moveNext = () => {
    setCustomHeaderNewMonth(true);
  };
  const movePrevious = () => {
    setCustomHeaderNewMonth(false);
  };

  const renderExamples = () => {
    return (
      <Fragment>
        {renderCalendarWithPeriodMarkingAndDotMarking()}

      </Fragment>
    );
  };

  return (
    <Dialog
      onDismiss={() => props.setShowCalender(false)}
      visible={props.showCalender}
      containerStyle={{
        backgroundColor: "#fff",
        borderRadius: 8,
      }}>
      {renderExamples()}
      <View
        style={{
          flexDirection: "row",
          alignItems: "center",
          justifyContent: "space-between",
          paddingHorizontal: 16,
          paddingVertical: 12,
          borderWidth: 1,
          borderStyle: "solid",
          borderTopColor: "rgba(0, 53, 128, 0.15)",
          borderLeftColor: "transparent",
          borderRightColor: "transparent",
          borderBottomColor: "transparent",
          width: "100%"
        }}>
        <Button
          labelStyle={{ fontSize: 14, fontWeight: "500", color: colors.primary }}
          style={{ backgroundColor: "transparent" }}
          label='Tuần này' />
        <View style={{ flexDirection: "row", alignItems: "center", justifyContent: "center", columnGap: 8 }}>
          <Button
            borderRadius={4}
            labelStyle={{ fontSize: 14, fontWeight: "500", color: colors.text }}
            style={{ backgroundColor: "#F2F5F8" }}
            label='Đặt lại' />
          <Button
            borderRadius={4}
            onPress={() => props.setShowCalender(false)}
            labelStyle={{ fontSize: 14, fontWeight: "500", color: "#fff" }}
            style={{ backgroundColor: colors.primary }}
            label='Xác nhận' />
        </View>
      </View>
    </Dialog>
  );
};

export default CalendarScreen;

