const CreateContractScreen = (props: any) => {
  const [listService, setListService] = useState();
  const [isFreshing, setIsFreshing] = useState(false);
  const setServiceSelected = useMyContractStore((state: any) => state.setServiceSelected);

  useFocusEffect(useCallback(() => {
    (async () => {
      const data = await searchListServices()
      setListService(data)
    })()
  }, []))

  const handleSaveServiceSelected = (item: any) => {
    setServiceSelected(item);
    navigation.navigate('guildeLine', { title: item.flowName })
  }

  const { navigation } = props;
  const renderItemCreateContract = ({ item }: any) => {

    return (
      <Card
        height={100}
        borderRadius={16}
        margin-15
        backgroundColor="#FBFBFB"
        onPress={() => handleSaveServiceSelected(item)}
      >
        <View style={{ paddingHorizontal: 18, paddingVertical: 24, }}>
          <View style={{ marginBottom: 10, borderRadius: 12, backgroundColor: item.backgroundColor, alignItems: "center", justifyContent: "center" }} width={35} height={35}>
            <Image width={18} height={18} source={item.image} />
          </View>
          <Card.Section content={[{ text: item.flowName, text70: true, grey10: true }]} />
        </View>
      </Card>
    )
  }
  return (
    <View style={{ backgroundColor: "#fff", flex: 1 }}>
      <SafeAreaView>
        <Header label="Tạo hồ sơ vay" {...props} />
        {/* <TabComponent /> */}
        {listService && (
          <GridList
            refreshing={true}
            contentContainerStyle={{ marginTop: 20, height: "100%" }}
            data={listService}
            renderItem={renderItemCreateContract}
            numColumns={2}
          />
        )}
      </SafeAreaView>
    </View>
  )
}

export default CreateContractScreen;