const calculateInterest = (
  loanAmount: number,
  interestRate: number,
  numberOfMonths: number,
): number => {
  const interestPercentage = interestRate / 100 / numberOfMonths;
  return loanAmount * interestPercentage;
};

const calculateNextMonthInterest = (
  loanAmount: number,
  principalValue: number,
  interestValues: number,
  numberOfMonths: number,
): number => {
  const remainingPrincipal = loanAmount - principalValue;
  return remainingPrincipal * interestValues;
};

