import { FlatList, SafeAreaView, StyleSheet, View } from 'react-native';
import { GridList, Timeline } from 'react-native-ui-lib';
import { colors } from '../../../assets/color/color';
import { image_tick } from '../../../assets/images/images';
import InstallmentScheduleItem from './ActionSheet';
import React, { useState } from 'react';

const InstallmentSchedule = (listInstallment: any) => {
  const [firstItem, setFirstItem] = useState<any>([]);

  const RenderTimelineItem = (installmentObj: any) => {
    return (
      <Timeline
        bottomLine={
          installmentObj !== listInstallment[listInstallment.length - 1]
            ? {
              type: Timeline.lineTypes.SOLID,
              state:
                firstItem && installmentObj.status !== firstItem.status
                  ? Timeline.states.CURRENT
                  : Timeline.states.NEXT,
            }
            : undefined
        }
        point={
          installmentObj.status == 'Đã thanh toán'
            ? {
              state: Timeline.states.SUCCESS,
              color: colors.primary,
              type: Timeline.pointTypes.OUTLINE
            }
            : {
              color: 'gray',
              state: Timeline.states.NEXT,
            }
        }
        topLine={
          installmentObj !== listInstallment[0]
            ? {
              type: Timeline.lineTypes.SOLID,
              state:
                (installmentObj.status == 'Đã thanh toán')
                  ? Timeline.states.CURRENT
                  : Timeline.states.NEXT,
            }
            : undefined
        }>
        <InstallmentScheduleItem
          key={installmentObj.id}
          itemInstallmentSchedule={installmentObj}
        />
      </Timeline>
    );
  };

  return (
    <FlatList
      showsVerticalScrollIndicator={false}
      showsHorizontalScrollIndicator={false}
      data={listInstallment}
      renderItem={({ item }) => RenderTimelineItem(item)}
    />
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
});

export default InstallmentSchedule;
